.loader {
    display: flex;
    align-items: center;
    position: absolute;
    justify-content: center;
    z-index: 1;
    width: 100%;
    height: 100%;
    // background: #ecf0f1;
    background: rgba(0, 0, 0, 0.737);

    pointer-events: none;
}

.loader .loader_inner {
    bottom: 50%;
    right: 50%;
    width: 100px;
    height: 100px;
    background: #000;
    transform: translate(-50%, -50%);
}

.loader span {
    position: absolute;
    width: 40%;
    height: 40%;
    border-radius: 50%;
    animation: loading 2s ease infinite;
    background: #193848;
}

.loader span:nth-child(1) {
    background: #19a6bc;
    animation-delay: 0.5s;
}

.loader span:nth-child(2) {
    background: #f63d3a;
    animation-delay: 1s;
}

.loader span:nth-child(3) {
    background: #f0a543;
    animation-delay: 1.5s;
}

.loader span:nth-child(4) {
    background: #193848;
    animation-delay: 2s;
}

@keyframes loading {

    0%,
    100% {
        transform: translate(0);
    }

    25% {
        transform: translate(80px);
    }

    50% {
        transform: translate(80px, 80px);
    }

    75% {
        transform: translate(0, 80px);
    }
}

.loader.fadeOut {
    animation: fadeOut 1s alternate;
}

@keyframes fadeOut {
    to {
        opacity: 0;
    }
}